body {
  font-family: MuseoSansCyrl, sans-serif;
  font-size: 15px;
  color: $dark;
  font-weight: 300;
  overflow-x:hidden;
}

h1, h2, h3 {
  text-transform: uppercase;
  font-family: Natasha, sans-serif;
  margin: 0;
  margin-bottom: 50px;
  letter-spacing: 5px;
  color:$dark;
}

.second-title{
  font-size: 37px;
}

.third-title{
  font-size: 22px;
  letter-spacing: 3px;
  color:rgba($dark, .5);
  margin-bottom: 36px;
}

ul, li {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

p{
  margin:  0;
}

img {
  min-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

header, section, footer {
  position: relative;
  padding-top: 76px;
}