.header {
  background: url("../img/header_bg.jpg") no-repeat center center / cover;
  height: 100vh;
  letter-spacing: 2px;
  padding: 0;

  .row:first-child {
    display: flex;
    align-items: center;
    height: 120px;
  }
}

.logo {
  font-size: 17px;
  text-transform: uppercase;
  color: #fff;
  white-space: nowrap;

  &__text {
    font-weight: 900;

    &_thin {
      font-weight: 100;
    }
  }
}

.menu {
  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    display: block;
  }

  &__link {
    color: #fff;
    transition: .2s ease;
    position: relative;
    font-weight: 900;

    &::after {
      display: block;
      content: '';
      height: 2px;
      width: 0;
      background: #fff;
      transition: .2s ease;
      position: absolute;
      bottom: -1px;
    }

    &:hover {
      color: darken(#fff, 20%);

      &::after {
        width: 100%;
        background: darken(#fff, 20%);;

      }
    }
  }
}

.mail {
  text-align: right;

  &__link {
    color: #fff;
    border-bottom: 1px solid #fff;
    font-size: 13px;
    font-weight: 300;
    transition: .2s ease;

    &:hover {
      font-size: 15px;
      color: #fff;
      text-shadow: 2px 2px 2px #fff;
    }
  }
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 120px - 110px);

  &__title {
    color: #fff;
    font-size: 49px;
    margin-bottom: 5px;
    white-space:nowrap;
  }

  &__title-text {
    color: #ffffff;
    font-size: 29px;
    font-weight: 100;
    text-transform: uppercase;
  }
}

.next-link {
  display: block;
  position: relative;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid #fff;
  bottom: 0;
  margin: 0 auto;
  transition: .2s ease;

  i {
    font-size: 16px;
    margin: -8px;
    top: 51%;
    left: 52%;
    color: #fff;
    position: absolute;
    transition: .2s ease;
  }

  &:hover {
    border-color: darken(#fff, 20%);
    background: rgba(#fff, .1);

    i {
      color: darken(#fff, 20%);
    }
  }
}

.bg__text {
  margin-bottom: 32px;

  &:last-child {
    margin-bottom: 0;
  }
}

.merits {
  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    display: block;
    width: 138px;
  }
}

.square-wrapper {
  width: 73px;
  height: 73px;
  padding: 7px;
  transform: rotate(45deg);
  margin-bottom: 26px;
  transition: .2s ease;
}

.square-inner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .2s ease;
}

.merit-int {
  transform: rotate(-45deg);
}

.decor-wrapper {
  border: 1px solid lighten($green, 15%);
}

.decor-inner {
  border: 1px solid lighten($green, 50%);
}

.decor-int {
  color: lighten($green, 15%);
  font-size: 20px;
  font-weight: 100;
}

.merit {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  transition: .2s ease;

  &:hover {
    transform: scale(1.3);

    .square-wrapper {
      background: lighten(#000, 75%);
    }

    .square-inner {
      background: #fff;
    }
  }
}

.contact-wrap {
  padding-top: 52px;
}

.contact {
  display: flex;
  justify-content: space-between;

  &__item {
    display: flex;
    align-items: center;
    height: 30px;
  }

  &__link {
    border-bottom: 1px solid lighten($green, 5%);
    color: $green;
    text-transform: lowercase;
    font-weight: 300;
    font-size: 14px;
    transition: .2s ease;
    margin: 2px 0;
    display: inline-block;

    &:hover {
      font-size: 16px;
      color: lighten($green, 10%);
      border-bottom: 1px solid lighten($green, 15%);
    }
  }
}

.write-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.me {
  display: flex;
  width: 64px;
  height: 64px;
  justify-content: center;
  align-items: center;
  background: darken(#fff, 15%);
  border-radius: 50%;
  font-size: 30px;
  color: $green;
  transition: .2s ease;
  margin-right: 20px;

  &:hover {
    color: lighten($green, 10%);
    background: darken(#fff, 30%);
  }
}

.write {
  margin-right: 30px;
}

.skills {
  &__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
    width: 240px;
    transition: .2s ease;

    &:hover {
      transform: scale(1.3);

      .skills__round-wrapper {
        background: lighten(#000, 75%);
      }

      .skills__round-inner {
        background: #fff;
      }
    }
  }

  &__round-wrapper {
    width: 61px;
    height: 61px;
    padding: 7px;
    border-radius: 50%;
    transition: .2s ease;
  }

  &__round-inner {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: .2s ease;
  }

  &__round_int {
    font-size: 13px;
  }

  &__name {
    font-weight: 900;
    margin-left: 23px;
  }
}


.work {
  &__item {
    width: 100%;
    overflow: hidden;
    transition: .2s ease;

    &-wrap{
      overflow: hidden;
    }

    &:hover {
      transform: scale(1.15);
    }
  }

  &__overflow {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background: rgba(#000, .5);
    transition: .2s ease;
    opacity: 0;

    &:hover {
      opacity: 1;
    }
  }

  &__title {
    font-size: 15px;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #ffffff;
  }

  &__link {
    font-size: 13px;
    font-weight: 300;
    color: #ffffff;
    text-decoration: underline;
    padding: 5px;

    &:hover {
      font-size: 16px;
      color: #ffffff;
      text-decoration: underline;
      padding: 2px;
    }
  }
}

@for $i from 1 through 3 {
  .work-pic-#{$i} {
    background: url("../img/pic_#{$i}.jpg") no-repeat center center / cover;
  }
}

.footer {
  padding-bottom: 103px;
  padding-top: 113px;

  &__text {

  }
}