@media screen and (max-width: 1199px) {

}

@media screen and (max-width: 991px) {
  .main__title {
    font-size: 44px;
  }

  h2, h3 {
    text-align: center;
  }

  .bg__text {
    text-align: justify;
  }

  .merits {
    padding-top: 50px;
  }

  .contact {
    flex-direction: column;
  }

  .contact__link {
    font-size: 18px;
  }

  .contact__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
  }

  .skills__item {
    width: auto;
  }

  .write {
    margin-right: 0;
  }

  .write-wrap {
    width: 100%;
  }

  .footer__text {
    text-align: center;
  }
}

@media screen and (max-width: 767px) {
  .header{
    background-position-x: -487px;
  }

  .main__title {
    font-size: 31px;
  }

  .main__title-text {
    font-size: 24px;
  }

  .write-wrap {
    flex-direction: column;
  }

  .me{
    margin-right: 0;
    margin-bottom: 15px;
  }

  .skills__list {
    flex-wrap: wrap;
  }

  .skills__item {
    width: 50%;
    margin-bottom: 20px;
  }

  .skills__item:nth-child(3),
  .skills__item:nth-child(4){
    margin-bottom: 0;
  }

  .work__item{
    margin: 30px auto 0 auto;
  }

}

@media screen and (max-width: 575px) {
  .logo{
    text-align: center;
  }

  .main__title{
    white-space: normal;
    text-align: center;
    font-size: 45px;
  }

  .main__title-text{
    text-align: center;
    font-size: 23px;
  }

  .merits__list{
    flex-direction: column;
    align-items: center;
  }

  .merits__text:last-child{
    margin-bottom: 30px;
  }

  .contact__list{
    flex-direction: column;
  }

  .skills__list{
    flex-direction: column;
  }
  .skills__item{
    width: 100%;
    margin: 0;
    flex-direction: column;
  }
  .skills__name{
    margin: 15px 0 30px 0;
  }
}